* {
  color: #ffff;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.section-login {
  width: 100%;
  height: 100vh;
  background: #090909;
  display: flex;
  align-items: center;
  justify-content: center;
}

.container-login {
  width: 746px;
  height: 413px;
  border-radius: 24px;
  background: rgba(255, 255, 255, 0.2);
  backdrop-filter: blur(50px);
  display: flex;
  flex-direction: column;
}

.container-login .image {
  /* height: 413px; */
  background: linear-gradient(180deg, #00cfb0 0%, #0057b2 100%);
  border-radius: 24px;
}

.container-login .submit {
    margin: 40px 37px;
    width: 373px;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: space-evenly;
    left: 10px;
    align-content: center;
}

.submit .title {
  font-size: 50px;
  line-height: 30px;
  text-align: center;
  position: absolute;
  top: 50px;
  left: 135px;
}

.submit .subtitle {
  font-size: 15px;
  line-height: 35px;
  text-align: center;
  position: absolute;
  top: 105px;
  left: 0px;
}

.submit .user {
  height: 44px;
  width: 250px;
  border-radius: 4px;
  text-align: inherit;
  border-style: none;
  background: rgba(255, 255, 255, 0.01);
  box-shadow: inset 5px -5px 20px rgba(76, 76, 79, 0.4),
    inset -5px 5px 20px rgba(3, 3, 3, 0.5);
}

.submit input[type='text'] {
  padding-left: 10px;
}

.submit .linker-page {
  width: 175px;
  height: 38px;
  background: #d9d9d9;
  border-radius: 4px;
  border-style: none;
  margin-top: 20px;
}
.submit .linker-page a{
  color: black;
  text-decoration: none;
}
.btn-login {
  display: flex;
  background-color: rgb(157, 164, 157);
  color: rgb(9, 9, 9);
  border-radius: 8px;
  cursor: pointer;
  margin: 0 50px 5px 0px;
  padding: 5px;
}
